.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn {
  margin: 10px;
}
.Home {
  text-decoration: none;

  margin: 10px;
}

.Home .text-dark {
  color: white;
}
.Home .text-light {
  color: black;
}
.About {
  text-decoration: none;

  margin: 10px;
}
.About .text-light {
  color: black;
}
.About .text-dark {
  color: white;
}

.navbar{
  display:flex;
  justify-content: space-between;
  align-items: center;
  background-color: Black;
}

.form-check {
  margin-left: auto;
}
